  const handleReviseButton = (proposalID: string
    , clientName: string
    , clientID: string
    , ticketID: string
  ) => {
    setProposalToDownload(proposalID);
    setRegisteredNametoDownload(clientName);
    setClientIDtoDownload(clientID);

    if (ticketID) { setProposalToDownload(ticketID); }
    setRedirectType('revision');
    setRedirect(true);
  }
  const handleActionButton = (status: string
    , proposalID: string
    , clientName: string
    , clientID: string
    , ticketID: string
    , fileArray: any[]
  ) => {
    console.log(status, proposalID, clientID, ticketID);
    setProposalToDownload(proposalID);
    setRegisteredNametoDownload(clientName);
    setClientIDtoDownload(clientID);
    switch (status) {
      case "Submitted":
        setRedirectType('submitted')
        setRedirect(true)
        break;
      case "Resubmitted":
        setRedirectType('submitted')
        setRedirect(true)
        /* previous implementation (view pdf) */
        // setLoading(true)
        // setRenderAction('view')
        // setRenderType('renderProposal')
        // setDownloadProposal(true)
        break;
      case 'For Approval':
        if (is_manager) {
          if (ticketID) {
            setTicketToDownload(ticketID);
            setRedirectType('for_approval');
            setRedirect(true);
          }
        } else {
          setRedirectType('disapprove')
          setRedirect(true)
        }
        break;
      case 'Expired':
      case 'Awaiting Activation':
      case "Active":
        setRedirectType('active')
        setRedirect(true)
        /*setRenderAction('view')
        setRenderType('renderContract')
        setDownloadProposal(true)*/
        break;
      case "Approved":
        setRenderAction('download')
        setRenderType('renderProposal')
        setDownloadProposal(true)
        setLoading(true)
        break;
      case "Signed":
        setLoading(true);
        handleSignedAction(fileArray);
        break;
      case "For Revision":
        setRedirectType('disapprove')
        setRedirect(true)
        // if (ticketID) { setProposalToDownload(ticketID); }
        // setRedirectType('revision')
        // setRedirect(true)
        break;
      case "Disapproved":
        setRedirectType('disapprove')
        setRedirect(true)
        break;

    }
  }

  const handleSignedAction = (files: any[]) => {
    /*files.forEach(item=>{
      console.log('Action Button',item)

    })*/
    for (let file of files) {
      //console.log(file);
      if (file['url']) {
        file['url'] = file['url'].replace('public/', API_URL)
        let a = document.createElement('a');
        a.href = file['url'];
        a.download = file['name'];
        a.click();
        setLoading(false);
      }
    }
  }
  const handleLoadProposalsFromAPI = (limit, skip) => {
    //let temp=[];
    setRenderTable(false);
    setLoading(true);
    console.log('load')
    console.log(JSON.stringify(filtersForApi));
    console.log(order);
    let withCount = true;
    if (skip == 0) {
      withCount = true;
    }

    Utils.StorageService('user_data').then((result) => {
      console.log('Utils', result);
      if (result && result !== undefined) {

        for (const i in result) {
          if (result[i]["key"] === "username") {
            username = result[i]["value"]
          }
          if (result[i]["key"] === "user_id") {
            user_id = result[i]["value"]
          }

          if (username.includes("underwriting_manager") || username.includes("sales_manager")) {
            is_manager = true;
          }

          if (username !== "" && user_id !== "" && username !== undefined
            && username.includes("bdo_user_")) {
            filtersForApi["where"]['custom_metadata.created_by'] = user_id
            break;
          }
        }
      }
      API.getProposal(filtersForApi, withCount, limit, skip, order).then(response => {
        console.log('Load From API:', response)
        if (response && response.proposals && response.error === undefined) {
          if (withCount) {
            if (response.total !== undefined) {
              totalCount['total'] = response.total;
            }
            if (response.status_active !== undefined) {
              totalCount['active'] = response.status_active;
            }
            if (response.status_approved !== undefined) {
              totalCount['approved'] = response.status_approved;
            }
            if (response.status_for_revision !== undefined) {
              totalCount['for_revision'] = response.status_for_revision;
            }
            if (response.status_signed !== undefined) {
              totalCount['signed'] = response.status_signed;
            }
            if (response.total !== undefined) {
              totalCount['totalrow'] = response.total;
            }
          }
          console.log('check')
          console.log(response);
          let tmp: any[] = [];
          response.proposals.map(col => {
            let bdo_broker = '-'
            if (col && col['client'] && col['client'][0] && col['client'][0]['sales_channel'] && col['client'][0]['sales_channel']['type']) {
              if (col['client'][0]['sales_channel']['type'] === 'broker' && col['client'][0]['sales_channel']['contact_person']) {
                bdo_broker = col['client'][0]['sales_channel']['contact_person']
              } else if (col['client'][0]['sales_channel']['type'] === 'bdo' && col['client'][0]['sales_channel']['bdo_name']) {
                bdo_broker = col['client'][0]['sales_channel']['bdo_name']
              } else if (col['client'][0]['sales_channel']['type'] === 'agent' && col['client'][0]['sales_channel']['agent_name']) {
                bdo_broker = col['client'][0]['sales_channel']['agent_name']
              }
            }
            is_creator = col.custom_metadata.created_by === user_id && col.custom_metadata.created_by != undefined ? true : false;
            const effdate = col.custom_metadata.coverage_start_date &&
              col.custom_metadata.coverage_end_date ?
              (new Date(
                col.custom_metadata.coverage_start_date)).toLocaleDateString('en-US',
                  {
                    year: 'numeric',
                    day: 'numeric',
                    month: 'short'
                  }) + ' - ' + (moment(col.custom_metadata.coverage_end_date).subtract(8,'hours').format('MMM, DD, YYYY')) : '-'
            tmp.push({
              account: col.client ? col.client[0].registered_name ? col.client[0].registered_name : '-' : '-',
              // account:col.client_registered_name ? col.client_registered_name : '-',
              version: col.version ? col.version : '-',
              proposal_no: col.proposal_number ? col.proposal_number : '-',
              effectivity_date: effdate,
              bdobroker: bdo_broker,
              // bdobroker:col.client[0].sales_channel && 
              //             col.client[0].sales_channel.contact_person && 
              //               col.client[0].sales_channel.contact_person !== ''  ? 
              //                 col.client[0].sales_channel.contact_person : '-',
              date_created: col.custom_metadata.date_created ?
                new Date(col.custom_metadata.date_created).toLocaleDateString('en-US',
                  {
                    year: 'numeric',
                    day: 'numeric',
                    month: 'short'
                  })
                : '-',
              date_modified: col.custom_metadata.date_updated ?
                new Date(col.custom_metadata.date_updated).toLocaleDateString('en-US',
                  {
                    year: 'numeric',
                    day: 'numeric',
                    month: 'short'
                  })
                : '-',
              status: col.status === 'Active' ? 'Closed' : col.status,
              action: col.status === 'Submitted' ||
                col.status === 'Disapproved' ||
                col.status === 'Resubmitted' ||
                col.status === 'Active' ||
                col.status === 'Signed' ||
                col.status === 'Expired' ||
                col.status === 'For Approval' ||
                col.status === 'Awaiting Activation' ?
                <Button
                  //disabled={true} 
                  style={{ backgroundColor: '#3AB77D', color: 'white', fontSize: '12px', padding: '2px' }}
                  onClick={() => { handleActionButton(col.status, col._id, col.client[0].registered_name, col.client_id, col.ticket_no, col.files) }}
                >
                  View
                            </Button> :
                col.status === 'Approved' ?
                  <Button
                    //disabled={true} 
                    style={{ backgroundColor: '#3AB77D', color: 'white', fontSize: '12px', padding: '2px' }}
                    onClick={() => { handleActionButton(col.status, col._id, col.client[0].registered_name, col.client_id, col.ticket_no, col.files) }}
                  >
                    Download
                            </Button> :
                  col.status === 'For Revision' && is_creator ?
                    <Button
                      //disabled={true} 
                      style={{ backgroundColor: '#3AB77D', color: 'white', fontSize: '12px', padding: '2px' }}
                      onClick={() => {
                        handleReviseButton(col._id,
                          col.client[0].registered_name, col.client_id, col.ticket_no)
                      }}
                    >
                      Revise
                            </Button> :
                    col.status === 'For Revision' && !is_creator ?
                      <Button
                        //disabled={true} 
                        style={{ backgroundColor: '#3AB77D', color: 'white', fontSize: '12px', padding: '2px' }}
                        onClick={() => { handleActionButton(col.status, col._id, col.client[0].registered_name, col.client_id, col.ticket_no, col.files) }}
                      >
                        View
                            </Button> :
                      <Button
                        //disabled={true} 
                        style={{ backgroundColor: '#3AB77D', color: 'white', fontSize: '12px', padding: '2px' }}
                        onClick={() => { handleActionButton(col.status, col._id, col.client[0].registered_name, col.client_id, col.ticket_no, col.files) }}
                      >
                        Upload Signed Proposal
                            </Button>
            })
          })
          console.log('tmp: ', tmp);
          // setSelectedRows(tmp);
          setRows(tmp);
          setRenderTable(true);
          setLoading(false);
        } else {
          totalCount['total'] = 0;
          totalCount['active'] = 0;
          totalCount['approved'] = 0;
          totalCount['for_revision'] = 0;
          totalCount['signed'] = 0;
          totalCount['totalrow'] = 0;
          setRows([])
          setLoading(false);
          setRenderTable(true)
          console.log('getbenefitlist api error: ', response);
        }
      });
    })
      .catch(error => console.log(error))

    setRenderTable(true)
  }
